import React,{Component} from 'react';

import './SideMenu.css';
import NavItem from './NavItem';

class SideMenu extends Component{
    render(){
        return(
            <nav id="slide-menu">
                <ul>
                    <NavItem id={"collection"} name={""} seprate={false}/>
                    <NavItem id={"Works1"} name={"FAT"} seprate={false}/>
                    <NavItem id={"Works2"} name={"Cut The Rope"} seprate={false}/>
                    <NavItem id={"Works3"} name={"App"} seprate={false}/>
                    <NavItem id={"Works4"} name={""} seprate={false}/>
                    <NavItem id={"Works5"} name={""} seprate={false}/>
                    <NavItem id={"Works6"} name={""} seprate={false}/>
                    <NavItem id={"Works7"} name={"Pet Vacay()"} seprate={false}/>
                    <NavItem id={"Works8"} name={"Issue Tracking System"} seprate={false}/>
                    <NavItem id={"profile"} name={""} seprate={true}/>
                    <NavItem id={"experience"} name={""} seprate={false}/>
                </ul>
            </nav>
        )
    }
}

export default SideMenu;


